.course {
  opacity: 1.0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: height 250ms 0ms, opacity 250ms 250ms;
  -o-transition: height 250ms 0ms, opacity 250ms 250ms;
  transition: height 250ms 0ms, opacity 250ms 250ms;
}

.course.remove {
  border: solid 1px #2bbbad;
  -webkit-transition: height 250ms 250ms, opacity 250ms 0ms;
  -o-transition: height 250ms 250ms, opacity 250ms 0ms;
  transition: height 250ms 250ms, opacity 250ms 0ms;
  opacity: 0.0;
  height: 0px;
}

.course.add {
  border: solid 1px #2bbbad;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: height 250ms 0ms, opacity 250ms 250ms;
  -o-transition: height 250ms 0ms, opacity 250ms 250ms;
  transition: height 250ms 0ms, opacity 250ms 250ms;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);

}

.modal-main {
  position: fixed;
  background: white;
  width: 80%;
  height: auto;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}


